import React, { useEffect, useState } from "react";
import Cookies from "js-cookie";
import {
  Card,
  CardTitle,
  CardBody,
  Table,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Button,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
const id = Cookies.get('id');
const UserDetails = () => {
  const [userDetails, setUserDetails] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);
  const [filter, setFilter] = useState("all"); // Default filter is "all"
  const [dropdownOpen, setDropdownOpen] = useState(false);

  useEffect(() => {
    // Fetch user details from the API
    fetch("http://15.206.66.176:8080/user/user-details")
      .then((res) => res.json())
      .then((data) => setUserDetails(data))
      .catch((err) => console.log(err));
  }, []);

  // Function to handle user click and open the modal
  const handleUserClick = (user) => {
    setSelectedUser(user);
    setModalOpen(true);
  };

  // Function to close the modal
  const closeModal = () => {
    setSelectedUser(null);
    setModalOpen(false);
  };

  // Function to handle filter change
  const handleFilterChange = (newFilter) => {
    setFilter(newFilter);
    setDropdownOpen(false); // Close the dropdown after selecting an option
  };

// Function to toggle ban/unban status for a user
const toggleUserStatus = (userId, currentStatus) => {
    console.log(`Toggling status for user with ID: ${userId}`);
    console.log(`Current status: ${currentStatus}`);
    // Determine the new status based on the current status
    const newStatus = currentStatus === 1 ? 0 : 1;
    console.log(`New status: ${newStatus}`);
  
    // Construct the URL with the user ID
    const apiUrl = `http://15.206.66.176:8080/user/userBanUnbanToggle/${id}/${userId}`;
  
    // Send a GET request to toggle the status
    fetch(apiUrl, {
      method: "GET",
    })
      .then((res) => {
        if (res.status === 200) {
          console.log("Status toggle successful.");
          // Update the user's status in the local state
          const updatedUserDetails = userDetails.map((user) =>
            user.id === userId ? { ...user, isBanned: newStatus } : user
          );
          setUserDetails(updatedUserDetails);
        } else {
          console.error("Error toggling status.");
        }
      })
      .catch((err) => console.error("Error toggling status:", err));
  };  

  // Filter the user list based on the selected filter
  const filteredUsers = () => {
    if (filter === "all") {
      return userDetails;
    } else if (filter === "banned") {
      return userDetails.filter((user) => user.isBanned === 1);
    } else if (filter === "premium") {
      return userDetails.filter((user) => user.isPremium === 1);
    }
  };

  return (
    <Card>
      <CardTitle tag="h4" className="text-center border-bottom p-3 mb-0">
        <i className="bi bi-person-fill-lock me-2"></i>
        Unverified Properties
      </CardTitle>
      <CardTitle tag="h6" className="p-3 mb-0">
        <i className="bi bi-person-fill-lock me-2"></i>
        Verify Property
      </CardTitle>
      <CardBody style={{ overflowX: "auto" }}>
        <div className="d-flex justify-content-end mb-3">
          <Dropdown
            isOpen={dropdownOpen}
            toggle={() => setDropdownOpen(!dropdownOpen)}
          >
            <DropdownToggle caret>Filter Users</DropdownToggle>
            <DropdownMenu>
              <DropdownItem onClick={() => handleFilterChange("all")}>
                All Users
              </DropdownItem>
              <DropdownItem onClick={() => handleFilterChange("banned")}>
                Banned Users
              </DropdownItem>
              <DropdownItem onClick={() => handleFilterChange("premium")}>
                Premium Users
              </DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </div>
        <h1>User Details</h1>
        <Table striped bordered hover responsive>
          <thead>
            <tr>
              <th>#</th>
              <th>User ID</th>
              <th>Name</th>
              <th>Number</th>
              <th>Email</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {filteredUsers().map((user, index) => (
              <tr key={user.id} onClick={() => handleUserClick(user)}>
                <td>{index + 1}</td>
                <td>{user.id}</td>
                <td>{user.name}</td>
                <td>{user.number || "-"}</td>
                <td>{user.email || "-"}</td>
                <td>
                  {user.isBanned === 1 ? (
                    <Button
                      color="primary"
                      onClick={(e) => {
                        e.stopPropagation();
                        toggleUserStatus(user.id, user.isBanned);
                      }}
                    >
                      Unban
                    </Button>
                  ) : (
                    <Button
                      color="danger"
                      onClick={(e) => {
                        e.stopPropagation();
                        toggleUserStatus(user.id, user.isBanned);
                      }}
                    >
                      Ban
                    </Button>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      </CardBody>

      {/* Modal to display user details */}
      <Modal isOpen={modalOpen} toggle={closeModal}>
        <ModalHeader toggle={closeModal}>User Details</ModalHeader>
        <ModalBody>
          {selectedUser && (
            <div>
              <p>User ID: {selectedUser.id}</p>
              <p>Name: {selectedUser.name}</p>
              <p>Number: {selectedUser.number}</p>
              <p>Email: {selectedUser.email || "-"}</p>
              <p>Contacted Properties:</p>
              <ul>
                {selectedUser.contacted_property &&
                  selectedUser.contacted_property.map((propertyId) => (
                    <li key={propertyId}>{propertyId}</li>
                  ))}
              </ul>
              {/* Add more user details here */}
            </div>
          )}
        </ModalBody>
        <ModalFooter>
          <Button color="secondary" onClick={closeModal}>
            Close
          </Button>
        </ModalFooter>
      </Modal>
    </Card>
  );
};

export default UserDetails;
