import "./UnverifiedProperties.css";
import { React, useEffect, useState } from "react";
import CustomCarousel from "../../components/CustomCarousel";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Cookies from "js-cookie";
import {
  faLocationDot,
  faArrowRight,
  faArrowLeft,
} from "@fortawesome/free-solid-svg-icons";
import { Link, useNavigate } from "react-router-dom";
import {
  Button,
  Card,
  CardTitle,
  CardBody,
  Modal,
  ModalHeader,
  ModalBody,
  Input,
} from "reactstrap";
const adminId = Cookies.get("id");
const AllProperties = () => {
  const [filteredProperties, setFilteredProperties] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [filterType, setFilterType] = useState("all");
  const [propertyCount, setPropertyCount] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [deleteReason, setDeleteReason] = useState("");
  const [propertyIdToDelete, setPropertyIdToDelete] = useState(null);

  useEffect(() => {
    const fetchProperties = () => {
      let apiUrl = "";
      switch (filterType) {
        case "rented":
          apiUrl = "http://15.206.66.176:8080/property/rentouted-properties";
          break;
        case "deleted":
          apiUrl = "http://15.206.66.176:8080/property/deleted-properties";
          break;
        default:
          apiUrl = "http://15.206.66.176:8080/property/Verified-properties";
      }

      fetch(apiUrl)
        .then((res) => {
          if (!res.ok) {
            // Handle non-OK response status here, e.g., show an error message
            throw new Error(`Failed to fetch properties: ${res.statusText}`);
          }
          return res.text(); // Read the response as text
        })
        .then((data) => {
          if (
            data === "No properties found." ||
            data === "No deleted properties found."
          ) {
            // Handle the case when no properties are found
            setFilteredProperties([]);
            setPropertyCount(0); // Set property count to 0
          } else {
            // Parse the JSON data if it's not an error message
            const jsonData = JSON.parse(data);
            setFilteredProperties(jsonData);
            setPropertyCount(jsonData.length); // Set property count
          }
        })
        .catch((err) => {
          console.error(err);
          // Handle the error here, e.g., show an error message to the user
        });
    };

    fetchProperties(); // Call the fetchProperties function within useEffect
  }, [filterType]);
  const handleToggle = async (id) => {
    const response = await fetch(
      `${process.env.REACT_APP_API_IP}/property/toggleStatus/${adminId}/${id}`
    );
    if (response.ok) {
      window.location.reload();
    } else {
      console.log(response);
    }
  };

  const handleFilter = (filter) => {
    setFilterType(filter);
  };

  const goToPreviousPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  const goToNextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };

  let totalPages = 1;
  let visibleData = filteredProperties;
  if (filteredProperties.length && filteredProperties.length > 10) {
    totalPages = Math.ceil(filteredProperties.length / 10);
    const startIndex = (currentPage - 1) * 10;
    const endIndex = startIndex + 10;
    visibleData = filteredProperties.slice(startIndex, endIndex);
  }
  const openModal = (propertyId) => {
    console.log("Opening modal for propertyId:", propertyId);
    setPropertyIdToDelete(propertyId);
    setModalOpen(true);
  };

  // Function to close the modal
  const closeModal = () => {
    console.log("Closing modal");
    setModalOpen(false);
  };

  const handleDeleteProperty = (propertyId) => {
    // Construct the URL with the adminId and propertyId
    const url = `http://15.206.66.176:8080/property/delete-property/${adminId}`;

    // Prepare the request body
    const requestBody = {
      id: propertyId,
      deleteReason: deleteReason,
    };

    // Send the POST request
    fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(requestBody),
    })
      .then((response) => {
        console.log("Response status:", response.status);
        // Handle the response here
        if (response.ok) {
          console.log("Property deleted successfully");
          // You can add code here to handle success, e.g., removing the property from the list
          // Reload the page
          window.location.reload();
        } else {
          console.error("Error deleting property");
          // Handle errors here
        }
      })
      .catch((error) => {
        console.error("Error:", error);
        // Handle errors here
      });

    // Close the modal after submitting
    closeModal();
  };
  const navigate = useNavigate();

  const handleEditPropertyButton = (id) => {
    navigate(`/edit-property?id=${id}`);  
  }

  return (
    <Card>
      <CardTitle tag="h4" className="text-center border-bottom p-3 mb-0">
        <i className="bi bi-person-fill-lock me-2"></i>
        Verified Properties
        <div className="property-count">Total Properties: {propertyCount}</div>
      </CardTitle>
      <CardBody>
        <div className="filter-buttons">
          <button onClick={() => handleFilter("all")}>All Properties</button>
          <button onClick={() => handleFilter("rented")}>Rented Out</button>
          <button onClick={() => handleFilter("deleted")}>
            Deleted Properties
          </button>
        </div>

        <div className="">
          <div className="header">{/* ... */}</div>
          <div className="row justify-content-center">
            {/* Main Contain */}
            <div className="mainContain py-2 col-md-12">
              <div className="mb-3 " style={{ maxWidth: "100%" }}>
                {visibleData.map((property, index) => (
                  <div
                    className="row shadow-sm no-gutters rounded-2"
                    key={index}
                    id="propertyCard"
                  >
                    <div className="col-md-4 p-0">
                      <div id="GenderBox">
                        <span className="">{property.memberedAllowed}</span>
                      </div>
                      <CustomCarousel photoUrls={property.photoUrls} />
                    </div>
                    <div className="col-md-6" id="propertyCard-body">
                      <Link
                        className="text-decoration-none text-dark"
                        to={`/paying-guest-in-ahmedabad/${property.id}`}
                      >
                        <div id="card-Heading">{property.title}</div>
                        <div id="card-location" className="row">
                          <div id="" className="col">
                            <FontAwesomeIcon
                              className="me-2 grey"
                              icon={faLocationDot}
                            />{" "}
                            {property.address && property.address.area}{" "}
                            Ahemdabad{" "}
                            {property.address && property.address.pincode}
                          </div>
                          <div className="LaptopHide col">
                            Type : {property.furnishedType}
                          </div>
                        </div>
                        <div id="card-Details" className="row">
                          <div id="Details" className="col">
                            <span className="grey">Available For :</span>{" "}
                            {property.memberedAllowed}
                          </div>
                          <div id="Details" className="col">
                            <span className="grey">Property Type :</span>{" "}
                            {property.subtype}
                          </div>
                        </div>
                        <div id="card-Details" className="row">
                          <div id="Details" className="col">
                            <span className="grey">Property Size :</span>{" "}
                            {property.bedroom} BHK
                          </div>
                          <div id="Details" className="col">
                            <span className="grey">Status :</span> Available
                          </div>
                        </div>
                        <div id="emnities" className=" mobileAndTab-hide">
                          {property.amenities.includes("electricity") && (
                            <img
                              className="amenities-size"
                              src="/emenities/electricity.svg"
                              alt="Amenities"
                            />
                          )}
                          {property.amenities.includes("cctv") && (
                            <img
                              className=" amenities-size"
                              src="/emenities/cctv.svg"
                              alt="Amenities"
                            />
                          )}
                          {property.amenities.includes("cooking") && (
                            <img
                              className=" amenities-size"
                              src="/emenities/gas-included.svg"
                              alt="Amenities"
                            />
                          )}
                          {property.amenities.includes("24*7-water") && (
                            <img
                              className=" amenities-size"
                              src="/emenities/water-included.svg"
                              alt="Amenities"
                              style={{ color: "red" }}
                            />
                          )}
                          {property.amenities.includes("house-keeping") && (
                            <img
                              className=" amenities-size"
                              src="/emenities/house cleaning- keeping.webp"
                              alt="Amenities"
                              style={{ color: "red" }}
                            />
                          )}
                          {property.amenities.includes("2-wheeler-parking") && (
                            <img
                              className=" amenities-size"
                              src="/emenities/parking-area.svg"
                              alt="Amenities"
                              style={{ color: "red" }}
                            />
                          )}
                          {property.amenities.includes("fans") && (
                            <img
                              className=" amenities-size"
                              src="/emenities/fans.svg"
                              alt="Amenities"
                              height="30px"
                              width="30px"
                              style={{ color: "red" }}
                            />
                          )}
                        </div>
                        <div id="rentBox" className="row">
                          <div className="col p-0 mobileAndTab-hide">
                            <b
                              style={{
                                fontSize: "23px",
                                color: "rgba(0, 0, 0, 0.6)",
                              }}
                            >
                              {property.rent
                                ? ` ${property.rent}`
                                : ` ${property.rentEachHead}`}
                            </b>{" "}
                            <span className="grey">/per month</span>
                          </div>
                          <div
                            className="col p-0 mobileAndTab-hide "
                            style={{ color: "rgba(0, 0, 0, 0.7)" }}
                          >
                            {" "}
                            <span className="grey">Deposit </span>:{" "}
                            {property.deposit} Rent{" "}
                          </div>
                        </div>
                      </Link>
                    </div>
                    <div className="col-md-2 rounded-4 " id="card-ButtonBox">
                      <div className="ownerBox">
                        <span>Owner :</span>
                        <br /> {property.name}
                      </div>
                      <div className="LaptopHide">
                        <b
                          style={{
                            marginLeft: "2px",
                            fontSize: "20px",
                            color: "rgba(0, 0, 0, 0.6)",
                          }}
                        >
                           {property.rent}
                        </b>{" "}
                        <span className="grey" style={{ fontSize: "12px" }}>
                          /per month
                        </span>
                      </div>
                      {property.isDeleted === 0 && (
                        <Button
                          id="contact-owner"
                          onClick={() => openModal(property.id)}
                        >
                          Delete Property
                        </Button>
                      )}
                      <Button className=" mobileAndTab-hide" id="send-message" onClick={() => handleEditPropertyButton(property.id)}>
                        Edit Property
                      </Button>
                    </div>
                    <div className="rounded-4 " id="bottomBar">
                      <div className="LaptopHide">
                        <b
                          style={{
                            marginLeft: "2px",
                            fontSize: "20px",
                            color: "rgba(0, 0, 0, 0.6)",
                          }}
                        >
                           {property.rent || property.totalRent}
                        </b>{" "}
                        <span className="grey" style={{ fontSize: "12px" }}>
                          /per month
                        </span>
                      </div>
                      <div className=" md-fs-5">
                        Property Status :{" "}
                        {property.isVerified ? (
                          !property.isRentedout ? (
                            <span className="text-success fw-bold">Live</span>
                          ) : (
                            <span className="text-danger fw-bold">
                              Rent Out
                            </span>
                          )
                        ) : (
                          <span className="text-warning fw-bold">
                            Under Review
                          </span>
                        )}
                      </div>
                      <div className="toggle-container ">
                        <p className="toggle-text ms-0">Available</p>
                        <label className="toggle-switch">
                          <input
                            type="checkbox"
                            checked={property.isRentedout}
                            onChange={() => {
                              handleToggle(property.id);
                            }}
                          />
                          <span className="slider"></span>
                        </label>
                        <p className="toggle-text">Rent Out</p>
                      </div>
                    </div>
                  </div>
                ))}
                {filteredProperties.length > 10 && (
                  <div className="paginationBox">
                    <Button
                      className="paginationBtn"
                      onClick={goToPreviousPage}
                    >
                      {" "}
                      <FontAwesomeIcon icon={faArrowLeft} /> Previous{" "}
                    </Button>
                    <Button className="paginationBtn" onClick={goToNextPage}>
                      Next <FontAwesomeIcon icon={faArrowRight} />
                    </Button>
                  </div>
                )}
              </div>
            </div>
          </div>
          {/* Modal for delete reason input */}
          <Modal isOpen={modalOpen} toggle={closeModal}>
            <ModalHeader toggle={closeModal}>Enter Delete Reason</ModalHeader>
            <ModalBody>
              <Input
                type="text"
                placeholder="Delete Reason"
                value={deleteReason}
                onChange={(e) => setDeleteReason(e.target.value)}
              />
              <Button
                color="danger"
                onClick={() => handleDeleteProperty(propertyIdToDelete)}
              >
                Confirm Delete
              </Button>
            </ModalBody>
          </Modal>
        </div>
      </CardBody>
    </Card>
  );
};

export default AllProperties;
