import "./sales.css";
import { useEffect, useState } from "react";
import axios from "../../api/axios";

const Sales = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await axios("/api/v1/paymentGateway/payment-details");
        // setData(res.data);
        const filteredData = res.data.filter(
          (item) =>
            item.paymentStatus === "Pending" ||
            item.paymentStatus === "Failed" ||
            item.paymentStatus === "Cancel"
        );
        setData(filteredData);
      } catch (error) {
        console.log(error);
      }
    };
    fetchData();
  }, []);

  return (
    <div className="App">
      <h1>Sales</h1>
      <br />
      <br />
      <table>
        <thead>
          <tr>
            <th>Index No.</th>
            <th>Customer ID</th>
            <th>Amount</th>
            <th>Plan</th>
            <th>Order ID</th>
            <th>Status</th>
            <th>Created On</th>
          </tr>
        </thead>
        <tbody>
          {data.map((item, index) => (
            <tr key={item.id}>
              <td>{index + 1}</td>
              <td>{item.customerId ? item.customerId : "-"}</td>
              <td>{item.amount ? item.amount : "-"}</td>
              <td>{item.plan ? item.plan : "-"}</td>
              <td>{item.orderId ? item.orderId : "-"}</td>
              <td style={{ color: "red" }}>
                {item.paymentStatus ? item.paymentStatus : "-"}
              </td>
              <td>{item.createdOn ? item.createdOn : "-"}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Sales;
